  padding-left: 10px;
  background-color: rgba(0, 0, 0, 0.5);
  line-height: 38px;
}

.picture{
  position: absolute;
  top: 140px; left: 215px;
  width: 120px; height: 120px;
  border-radius: 10%;
  box-shadow: black 3px 3px 10px;
  background-size: cover;
}

.description{
  color: white;
  margin: 0;
  font-size: 12px;
  position: absolute;
  top: 130px;
  left: 347px;
  width: 280px;
  height: 280px;
  line-height: normal;
  word-spacing: 2px;
  overflow-y: auto;
  text-align: justify;
  padding-right: 10px;
}

.topic{
  position: absolute;
  top: 280px;
  left: 230px;
  width: 95px;
  height: 20px;
  text-align: center;
  border-radius: 10px;
  color: #303030;
  background-color: white;
  box-shadow: inset #808080 -2px -2px 4px;
  font-size: 14px;
}

.topic:hover, .references:hover{
  background-color: #E0E0E0;
}

.topic:active, .references:active{
  box-shadow: inset #808080 2px 2px 4px;
}

.references{
  position: absolute;
  top: 330px;
  left: 230px;
  width: 95px;
  height: 20px;
  text-align: center;
  border-radius: 10px;
  color: #303030;
  background-color: white;
  box-shadow: inset #808080 -2px -2px 4px;
  font-size: 14px;
}

</style>



